@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:20px 0;padding:0;line-height:1;font-family:'Roboto', sans-serif, Arial;color:#202020;background-color:#fbfbfb;font-smooth:always;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.card{max-width:100%}.card .badge{font-size:16px}.card .text-desc{word-wrap:break-word;line-height:25px;font-size:medium;width:auto;height:auto}.card .text-name{word-wrap:break-word;align-items:center;text-align:center;width:auto;height:auto}.toast.show{position:absolute;left:20px;bottom:20px;z-index:10}.card .images{border-radius:20%;margin:25px;max-width:100%;height:auto}

.flex-container{padding:0;margin:0;list-style:none;-ms-box-orient:horizontal;display:-moz-flex;display:flex;flex:content}.nowrap{flex-wrap:nowrap}.wrap{flex-wrap:wrap}.wrap li{background:gold}.wrap-reverse{flex-wrap:wrap-reverse}.wrap-reverse li{background:deepskyblue}.flex-item{padding:5px;margin:10px;line-height:100px;text-align:center}.row{display:flex;flex-wrap:wrap}

.title{font-family:Arial, Helvetica, sans-serif;color:tomato;font-size:30px}.subtitle{font-family:Arial, Helvetica, sans-serif;color:#6832cd;font-size:20px}

